body {
  font-family: "Titillium Web", sans-serif;
  width: 100%;
  height: 100%;
  background-color: whitesmoke;
  color: #2e2e2e;
  font-size: 18px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 700;
}

h3 {
  font-size: 15px;
  display: block;
  margin-bottom: 8px;
  font-size: 2rem;
}

h2 {
  font-size: 2.2rem;
  margin-bottom: 25px;
  line-height: 42px;
  position: relative;
  font-weight: 800;
}

a {
  color: #c53d5a;
  text-decoration: none;
}
a:hover {
  color: #d1647b;
  text-decoration: underline;
}

.text-lead {
  font-size: 1.2em;
}

.text-cursive {
  font-family: "Amatic SC", cursive;
}

.wsk-hero {
  position: relative;
  padding: 60px 0 60px 0;
  background: #fff;
}
.wsk-hero__heading {
  font-size: 3em;
}
.wsk-hero__text {
  font-size: 1.3rem;
}
.wsk-hero__image {
  perspective: 1000px;
}
.wsk-hero__image img {
  width: 90%;
  border-radius: 10px;
  transform: rotateY(0deg);
}
.wsk-hero__content {
  border-radius: 0;
  position: relative;
  z-index: 1;
  text-align: left;
}
.wsk-hero__content p {
  margin: 40px 0;
}

@media (max-width: 767px) {
  .wsk-hero {
    padding: 50px 0;
  }
  .wsk-hero__content {
    padding: 0 10px;
    text-align: center;
  }
  .wsk-hero__image {
    display: none;
  }
}
@media only screen and (min-width: 768px) and (max-width: 991px) {
  .wsk-hero {
    padding: 60px 0;
  }
  .wsk-hero__content {
    text-align: center;
  }
  .wsk-hero__image {
    display: none;
  }
}
@media (min-width: 768px) {
  .wsk-features__item {
    height: 280px;
  }
}
@media (min-width: 992px) {
  .wsk-features__item {
    height: 300px;
  }
}
@media (min-width: 1200px) {
  .wsk-features__item {
    height: 250px;
  }
}
.wsk-features {
  background-color: #f9f9f9;
}
.wsk-features__item {
  text-align: left;
  padding: 35px;
  background-color: #fff;
  border-radius: 8px;
  position: relative;
  margin-top: 30px;
  transition: all 0.2s cubic-bezier(0.68, -0.55, 0.27, 1.55) 0s;
  box-shadow: 0px 3px 5px #00000017;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 20px 25px -5px, rgba(0, 0, 0, 0.04) 0px 10px 10px -5px;
}
.wsk-features__item:hover {
  box-shadow: 0px 3px 5px #00000017;
  transform: translateY(-5px);
}
.wsk-features__item i {
  height: 60px;
  width: 60px;
  line-height: 60px;
  text-align: center;
  display: block;
  background-color: #f64c71;
  color: #fff;
  font-size: 20px;
  border-radius: 4px;
  box-shadow: 0px 4px 6px #0000002a;
  float: left;
  margin-bottom: 10px;
  margin-right: 20px;
}
.wsk-features__item h3 {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 20px;
}
.wsk-features__item p {
  line-height: 25px;
  font-size: 1.1rem;
}

.wsk-cta__content {
  text-align: center;
  padding: 0px 50px;
}
.wsk-cta h2 {
  line-height: 50px;
  font-size: 38px;
  font-weight: 700;
  margin-bottom: 30px;
}
.wsk-cta p {
  padding: 0px 50px;
}

@media (max-width: 767px) {
  .wsk-cta__content {
    padding: 0;
  }
  .wsk-cta p {
    padding: 0;
  }
}
@media only screen and (min-width: 768px) and (max-width: 991px) {
  .wsk-cta__content {
    padding: 0;
  }
  .wsk-cta p {
    padding: 0;
  }
}
.wsk-section {
  padding: 60px 0;
}
.wsk-section__title {
  text-align: center;
  margin-bottom: 80px;
  position: relative;
  z-index: 5;
}

.wsk-btn {
  display: inline-flex;
  text-transform: capitalize;
  align-items: center;
  font-size: 1rem;
  font-weight: 600;
  padding: 13px 30px;
  border: none;
  transition: 0.2s;
  border-radius: 4px;
  position: relative;
  z-index: 1;
  margin-right: 7px;
  overflow: hidden;
  text-decoration: none;
  font-weight: 700;
  text-transform: uppercase;
  justify-content: center;
  margin-bottom: 20px;
}
.wsk-btn:hover {
  text-decoration: none;
}
.wsk-btn--primary {
  background-color: #f64c71;
  border: 2px solid #f64c71;
  color: #fff;
}
.wsk-btn--primary:hover {
  color: #fff;
  background-color: #f42552;
}
.wsk-btn--alt {
  background-color: transparent;
  border: 2px solid #f64c71;
  color: #f64c71;
}
.wsk-btn--alt:hover {
  color: #f64c71;
  border: 2px solid #f42552;
}
.wsk-btn--block {
  width: 100%;
}
.wsk-btn i {
  display: inline-block;
  margin-right: 5px;
}

@media (max-width: 767px) {
  .wsk-btn {
    width: 100%;
  }
}
.wsk-footer {
  background-color: #fff;
  padding: 20px;
  text-align: center;
}
.wsk-footer img {
  width: 200px;
}

.wsk-browser-compatibility {
  padding: 60px 0;
  background-color: #f2f2f2;
}
.wsk-browser-compatibility p {
  font-size: 1.3rem;
  margin-top: 10px;
}
.wsk-browser-compatibility .fi {
  font-size: 3rem;
}